}

.password-container {
	position: relative;
}
.fa-eye {
	position: absolute;
	top: 50%;
	right: 4%;
	cursor: pointer;
	color: lightgray;
}

.extra-form-text {
	width: 80%;
	height: 100%;
	margin: auto;
	color: black;
	font-size: 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-top: 10px;
}

#no-account a {
	color: #5048e5;
	cursor: pointer;
}

#no-password a {
	color: #5048e5;
	cursor: pointer;
}

