@media screen and (max-width: 640px) {
	.homepage-container {
		margin-bottom: 100px;
		padding-bottom: 100px;
	}
	.product-title {
		display: flex;
		flex-direction: column;
	}

	.title-right {
		display: flex;
		flex-direction: row;
	}
}
